/* AddBANK Modal*/
.addbank-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(153, 153, 153, 0.48);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  z-index: 999;
}

.addbank-modal:target {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.accountmodal-head {
  background-color: #fff;
  margin-left: auto;
  width: 438px;
  height: 100vh;
}

/* ADD BANK ACCOUNT STYLES*/
.bank-select {
  width: 380px;
  height: 56px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  border-radius: 4px;
  margin-top: 15px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.03em;
  color: #666666;
  padding-left: 25px;
  padding-right: 50px !important;
}

.bank_text {
  margin-top: 34px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 30px;
  letter-spacing: 0.03em;
  color: #121212;
  padding-left: 25px;
}
.button-group {
  padding-top: 30px;
}
/*CONFIRM BANK STYLES*/

.confirm-section {
  width: 90%;
  margin: auto;
}
.confirm-text {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 30px;
  letter-spacing: 0.03em;
  color: #121212;
  margin-top: 34px;
}

.confirm-details {
  width: 391px;
  height: 127px;
  background: #f4f8fd;
  border: 1px solid #64a0f7;
  box-sizing: border-box;
  border-radius: 4px;
  margin-top: 37px;
  text-align: center;
}

.confirm-details > h6 {
  padding-top: 20px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 19px;
  letter-spacing: 0.03em;
  color: #121212;
}

.confirm-texts {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 30px;
  letter-spacing: 0.03em;
  color: #666666;
  margin-top: 15px;
}

.confirm-access {
  font-size: 1.4rem;
  margin-top: -10px;
  color: #666666;
}

/*Success Page Style */
.confirmbank__success-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(153, 153, 153, 0.48);
  transition: all 0.3s;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.confirm__success-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 541px;
  height: 309px;
  background: #fff;
}
.success-info {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.bank-image {
  margin-top: 15px;
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.closemodal-btn {
  background: none;
  border: none;
  border-radius: none !important;
}

.confirm-close-modal {
  display: flex;
  justify-content: flex-end;
}

.bank-success-text {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.03em;
  color: #666666;
  margin-top: 32px;
}

