/* CARD MODAL STYLES*/
.bankcard-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(153, 153, 153, 0.48);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  z-index: 999;
}

.bankcard-modal:target {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.accountmodal-head {
  background-color: #fff;
  margin-left: auto;
  width: 438px;
  height: 100vh;
}

.bankCard-section {
  width: 90%;
  margin: auto;
}
.card-input {
  display: flex;
}

.input-card {
  width: 126px;
  height: 48px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  border-radius: 4px;
  margin-top: 25px;
  /* padding-left: 26px;
padding-right: 21px; */
}

.bankCard-section h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 30px;
  padding-top: 34px;
  letter-spacing: 0.03em;
  color: #121212;
}

.bankCard-section p {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  padding-top: 9px;
  letter-spacing: 0.03em;
  color: #666666;
}

.bank-item {
  width: 335px;
  height: 143px;
  border-radius: 8px;
  background-image: linear-gradient(180deg, #0768f6 0%, #5f53f2 100%);
  position: relative;
}

.bank-item p {
  margin: 0;
  overflow: hidden;
}

.bank-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.bank-list > * {
  margin-right: 27px;
  margin-bottom: 27px;
}

.bank-item .content,
.bank-item .delete-screen {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.03em;
  color: #fff;
}

.bank-item .content {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 14px 20px;
  background-image: url("../../../assets/bank-eclispe.svg");
  background-position: 236px 6px;
  background-repeat: no-repeat;
}

.bank-item .bank-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.bank-item .delete-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 32px 26px;
  background: #fafafa;
  color: #dd0707;
  opacity: 0;
  transition: 0.2s;
}

.bank-item .delete-screen:hover {
  opacity: 1;
}

.bank-item .delete-button {
  position: relative;
  z-index: 2;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
}

.bank-item .delete-icon {
  margin-right: 10px;
}
