.settings-section {
  width: 90%;
  margin: auto;
  overflow: hidden;
}

.settings-header h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.03em;
  color: #fd740e;
  margin-top: 24px;
}

.settings-header img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 38px;
}

.settings-details {
  display: flex;
  justify-content: space-between;
}

.settings-page {
  width: 393px;
  height: 43px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  cursor: pointer;
  margin-top: 42px;
}
